export default {
    init: () => {
        return {
            // 
            customerList: [],

            //  
            showRemarkFormDialogVisible: false,
            //  
            remarkForm: {
                Remarks: '',
                Id: ''
            },
            // 
            searchForm: {
                shipper: '',
                // 
                time: [],
                checked: false,
                pickerOptions: {
                    shortcuts: [{
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                            picker.$emit('pick', [start, end]);
                        }
                    }]
                },
            },
            // 
            restaurants: [],
            //   ""
            show: false,
            //  
            customerFormDialogVisible: false,
            // 
            setCustomerForm: {
                Shipper: '',
                Model: '',
                Name: '',
                Phone: '',
                Address: '',
                SaleDate: '',
                DeliveryDate: '',
                Remarks: '',
                Status: 1
            },

            // ,
            editCustomerFormDialogVisible: false,
            // 
            editCustomerForm: {
                Id: '',
                Shipper: '',
                Model: '',
                Name: '',
                Phone: '',
                Address: '',
                SaleDate: '',
                DeliveryDate: '',
                Remarks: '',
                Status: ''
            },

            // 
            formRules: {
                Shipper: [{
                    required: true,
                    message: '',
                    trigger: 'blur'
                }],
                Model: [{
                    required: true,
                    message: '',
                    trigger: 'blur'
                }],
                Name: [{
                    required: true,
                    message: '',
                    trigger: 'blur'
                }],
                Phone: [{
                    required: true,
                    message: '',
                    trigger: 'blur'
                }],
                Address: [{
                    required: true,
                    message: '',
                    trigger: 'blur'
                }],
                SaleDate: [{
                    required: true,
                    message: '',
                    trigger: 'blur'
                }],
                DeliveryDate: [{
                    required: true,
                    message: '',
                    trigger: 'blur'
                }],
            },
            // 
            outVisible: false,
            // 
            sendStockVisible: false,
              // 
              pickerOptions: {
                shortcuts: [{
                  text: '',
                  onClick(picker) {
                    picker.$emit('pick', new Date());
                  }
                }, {
                  text: '',
                  onClick(picker) {
                    const date = new Date();
                    date.setTime(date.getTime() - 3600 * 1000 * 24);
                    picker.$emit('pick', date);
                  }
                }, {
                  text: '',
                  onClick(picker) {
                    const date = new Date();
                    date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
                    picker.$emit('pick', date);
                  }
                }]
              },
        }
    }
}



