<template>
    <section>
        <!-- ---------------------------------------------------------------  ------------------------------------------------ -->
        <el-card style="margin-bottom: 5px;">
            <el-row :gutter="20" style="margin-bottom: 20px;">
                <el-form :model="searchForm" :inline="true" class="demo-form-inline" label-width="110px" ref="searchRef">
                    <el-form-item prop="time" label="">
                        <el-date-picker
                            unlink-panels
                            v-model="searchForm.time"
                            value-format="timestamp"
                            type="daterange"
                            range-separator=""
                            start-placeholder=""
                            end-placeholder=""
                            :picker-options="searchForm.pickerOptions"
                        >
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="" prop="shipper">
                         <el-autocomplete
                            class="inline-input"
                            v-model="searchForm.shipper"
                            :fetch-suggestions="querySearch"
                            placeholder=""
                            @focus="getShipper"
                            @select="handleSelect"
                            value-key="customerValue"
                        ></el-autocomplete>
                    </el-form-item>
                    <el-checkbox v-model="searchForm.checked"></el-checkbox>
                    <el-button type="primary" @click="search" icon="el-icon-search"></el-button>
                    <el-button @click="formClose('searchRef')" icon="el-icon-refresh-right"></el-button>
                </el-form>
            </el-row>
        </el-card>
        <!---------------------------------------------------------------------------------------------------------------->
        <el-card>
            <el-row>
                <el-row type="flex" justify="space-around">
                    <el-col :span="20">
                        <div></div>
                    </el-col>
                    <el-row type="flex" justify="end">
                        <el-button type="success" @click="customerFormDialogVisible = true" icon="el-icon-plus"></el-button>
                        <el-button type="info" @click="print" class="printButton" icon="el-icon-printer"></el-button>
                    </el-row>
                </el-row>
                <el-table :data="customerList" border style="width: 100%" :row-class-name="tableRowClassName">
                    <el-table-column prop="Shipper" label="" align="center"></el-table-column>
                    <el-table-column prop="SaleDate" label="" align="center" :formatter="dataFormatter"> </el-table-column>
                    <el-table-column prop="DeliveryDate" label="" align="center" :formatter="dataFormatter"> </el-table-column>
                    <el-table-column prop="Model" label="" align="center"></el-table-column>
                    <el-table-column prop="Name" label="" align="center"></el-table-column>
                    <el-table-column prop="Phone" label="" align="center"></el-table-column>
                    <el-table-column prop="Address" label="" align="center"></el-table-column>

                    <el-table-column prop="Status" label="" align="center">
                        <template slot-scope="scope">
                            <div>{{ scope.row.Status == 1 ? '' : '' }}</div>
                        </template>
                    </el-table-column>
                    <el-table-column prop="Remarks" label="" align="center" width="150px">
                        <template slot-scope="scope">
                            <el-input v-model="scope.row.Remarks" class="in" autosize @change="addRemark(scope.row)" type="textarea"> </el-input>
                        </template>
                    </el-table-column>
                    <!--  -->
                    <el-table-column scope align="center" label="" width="300px">
                        <template slot-scope="scope">
                            <!--  -->
                            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
                                <el-button icon="el-icon-setting" type="primary" @click="showEditForm(scope.row)" circle></el-button>
                            </el-tooltip>
                            <!--  -->
                            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
                                <el-button
                                    icon="el-icon-s-goods"
                                    type="success "
                                    @click="showSendStock(scope.row)"
                                    circle
                                    :disabled="scope.row.Status == 0 ? true : false"
                                ></el-button>
                            </el-tooltip>
                        </template>
                    </el-table-column>
                </el-table>
            </el-row>
        </el-card>

        <!---------------------------------------------------------------------------------------------------------------->
        <el-dialog title="" :visible.sync="customerFormDialogVisible" width="30%" @close="formClose('setCustomerForm')">
            <el-form ref="setCustomerForm" :model="setCustomerForm" label-width="170px" :rules="formRules">
                <el-form-item label="" prop="Shipper">
                    <el-input v-model="setCustomerForm.Shipper" label=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="Model">
                    <el-input v-model="setCustomerForm.Model"></el-input>
                </el-form-item>
                <el-form-item label="" prop="Name">
                    <el-input v-model="setCustomerForm.Name" label=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="Phone">
                    <el-input v-model="setCustomerForm.Phone" label=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="Address">
                    <el-input v-model="setCustomerForm.Address" label=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="SaleDate">
                    <el-date-picker
                        v-model="setCustomerForm.SaleDate"
                        type="date"
                        placeholder=""
                        format="yyyy  MM  dd "
                        value-format="timestamp"
                        :picker-options="pickerOptions"
                    >
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="" prop="DeliveryDate">
                    <el-date-picker
                        v-model="setCustomerForm.DeliveryDate"
                        type="date"
                        placeholder=""
                        format="yyyy  MM  dd "
                        value-format="timestamp"
                        :picker-options="pickerOptions"
                    >
                    </el-date-picker>
                </el-form-item>
                <el-form-item label=" : " prop="Remarks">
                    <el-input type="textarea" autosize v-model="setCustomerForm.Remarks" label=""></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="customerFormDialogVisible = false" icon="el-icon-close"></el-button>
                <el-button type="primary" @click="showCustomer" icon="el-icon-check"></el-button>
            </span>
        </el-dialog>

        <!---------------------------------------------------------------------------------------------------------------->
        <el-dialog title="" :visible.sync="editCustomerFormDialogVisible" width="30%" @close="formClose('editCustomerForm')">
            <el-form ref="editCustomerForm" :model="editCustomerForm" label-width="170px" :rules="formRules">
                <el-form-item label="" prop="Shipper">
                    <el-input v-model="editCustomerForm.Shipper" label=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="Model">
                    <el-input v-model="editCustomerForm.Model"></el-input>
                </el-form-item>
                <el-form-item label="" prop="Name" :rules="[{ required: true, message: '' }]">
                    <el-input v-model="editCustomerForm.Name" label=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="Phone">
                    <el-input v-model="editCustomerForm.Phone" label=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="Address">
                    <el-input v-model="editCustomerForm.Address" label=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="SaleDate">
                    <el-date-picker
                        v-model="editCustomerForm.SaleDate"
                        type="date"
                        placeholder=""
                        format="yyyy  MM  dd "
                        value-format="timestamp"
                        :picker-options="pickerOptions"
                    >
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="" prop="DeliveryDate">
                    <el-date-picker
                        v-model="editCustomerForm.DeliveryDate"
                        type="date"
                        placeholder=""
                        format="yyyy  MM  dd "
                        value-format="timestamp"
                        :picker-options="pickerOptions"
                    >
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="" prop="Status">
                    <div>
                        <el-checkbox v-model="editCustomerForm.Status" border label=""></el-checkbox>
                    </div>
                </el-form-item>
                <el-form-item label="" prop="Remarks">
                    <el-input type="textarea" v-model="editCustomerForm.Remarks" label="" autosize=""></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editCustomerFormDialogVisible = false" icon="el-icon-close"></el-button>
                <el-button type="primary" @click="EditForm" icon="el-icon-check"></el-button>
            </span>
        </el-dialog>

        <!-------------------------------------------------------  ------------------------------------------------------->
        <el-dialog title="" :visible.sync="sendStockVisible" width="30%" @close="formClose('setCustomerForm')">
            <el-form ref="setCustomerForm" :model="editCustomerForm" label-width="120px" :rules="formRules">
                <el-form-item label="" prop="Status">
                    <div>
                        <el-checkbox v-model="editCustomerForm.Status" border label=""></el-checkbox>
                    </div>
                </el-form-item>
                <el-form-item label="" prop="DeliveryDate">
                    <el-date-picker
                        v-model="editCustomerForm.DeliveryDate"
                        type="date"
                        placeholder=""
                        format="yyyy  MM  dd "
                        value-format="timestamp"
                    >
                    </el-date-picker>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="sendStockVisible = false" icon="el-icon-close"></el-button>
                <el-button type="primary" @click="sendStock" icon="el-icon-check"></el-button>
            </span>
        </el-dialog>

        <!-------------------------------------------------------  ------------------------------------------------------->
        <el-dialog title="" :visible.sync="outVisible" width="1086px" class="print">
            <div class="dialog-footer" style="text-align: center; margin-bottom : 15px">
                <h2 v-if="show == true"></h2>
                <el-button @click="outVisible = false" icon="el-icon-close"> </el-button>
                <el-button v-print="'#print'" type="info" @click="print" class="printButton" icon="el-icon-check"></el-button>
            </div>
            <div id="print">
                <table width="100%" height="100%" border="1px solid black">
                    <thead>
                        <tr>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                    </thead>
                    <tr v-for="item in customerList" :key="item.id">
                        <td class="printOhter">{{ item.Shipper }}</td>
                        <td class="printOhter">{{ dataForma(item.SaleDate) }}</td>
                        <td class="printOhter">{{ dataForma(item.DeliveryDate) }}</td>
                        <td class="printOhter">{{ item.Name }}</td>
                        <td class="printOhter">{{ item.Phone }}</td>
                        <td class="printModel">{{ item.Model }}</td>
                        <td class="printAddress">{{ item.Address }}</td>
                        <td class="printRemarks">{{ item.Remarks }}</td>
                    </tr>
                </table>
            </div>
        </el-dialog>
    </section>
</template>

<script>
import datas from './datas.js'
import methods from './methods.js'
import util from '../../common/js/util'
var mediaQueryList = window.matchMedia('print')

export default {
    data() {
        return datas.init()
    },
    created() {
        this.getList()
    },
    methods: methods
}
</script>

<style scoped media="print">
.el-table {
    margin-top: 15px;
}
.el-col {
    margin: 0 20px;
}
.el-button {
    margin: 0 6px;
}

.coll {
    position: relative;
}

.el-table /deep/ .over {
    background-color: #ffd700;
}

.el-table /deep/ .cell {
    white-space: pre-wrap;
    text-align: center;
    padding: 0px;
}

.printTable {
    margin-top: 0;
}

.printTable /deep/ td {
    padding: 8px 0px;
}
@page {
    size: A4 landscape;
    margin: 10px;
}

#print table {
    border-collapse: collapse;
    border-spacing: 0;
    text-align: center;
    font-size: 14px;
    white-space: pre-wrap;
}

#print table thead {
    font-weight: bold;
}

#print table td {
    padding: 3px;
}

.printModel {
    width: 130px;
}
.printRemarks {
    width: 170px;
}

.printOhter {
    width: 100px;
}
.printAddress {
    width: 300px;
}

.el-card /deep/ .el-card__body {
    padding: 5px;
    padding-top: 10px;
    margin-top: 10px;
}

.in /deep/ .el-textarea__inner {
    border: none;
    resize: none;
    padding: 0;
    font-size: 14px;
}

.el-form /deep/ .el-input__inner {
    width: 240px;
}
.el-form /deep/ .el-input-number {
    width: 240px;
}

.printButton {
    background-color: #bf1f1f;
    border-color: #bf1f1f;
}
</style>



