export default {
    init : () => {
        return {
            // 
            outStockList: [],
            date: '',
            // 
            addFormRules: {
                name: [{
                    required: true,
                    message: '',
                    trigger: 'blur'
                }],
                cate: [{
                    required: true,
                    message: '',
                    trigger: 'blur'
                }],
                brand: [{
                    required: true,
                    message: '',
                    trigger: 'blur'
                }],
                mod: [{
                    required: true,
                    message: '',
                    trigger: 'blur'
                }],
                price: [{
                    required: true,
                    message: '',
                    trigger: 'blur'
                }],
                num: [{
                    required: true,
                    message: '',
                    trigger: 'blur'
                }]
            },
            //  
            showRemarkFormDialogVisible: false,
            //  
            remarkForm: {
                Remarks: '',
                Id: ''
            },
            // 
            searchForm: {
                shipper: '',
                // 
                time: [],
                pickerOptions: {
                    shortcuts: [{
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                            picker.$emit('pick', [start, end]);
                        }
                    }]
                },
            },
            // 
            restaurants: [],
        }
    }
}



