<template>
    <section>
        <el-card style="margin-bottom: 5px;">
            <!-- ---------------------------------------------------------------  ------------------------------------------------ -->
            <el-row :gutter="20" style="margin-bottom: 20px;">
                <el-form :model="searchForm" :inline="true" class="demo-form-inline" label-width="110px" ref="searchRef">
                    <el-form-item prop="time" label="">
                        <el-date-picker
                            v-model="searchForm.time"
                            type="daterange"
                            range-separator=""
                            start-placeholder=""
                            end-placeholder=""
                            value-format="timestamp"
                            unlink-panels
                            :picker-options="searchForm.pickerOptions"
                        >
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="" prop="keyword">
                        <el-autocomplete
                            class="inline-input"
                            v-model="searchForm.keyword"
                            :fetch-suggestions="querySearch"
                            placeholder=""
                            @focus="getProvider"
                            @select="handleSelect"
                            value-key="stockValue"
                        ></el-autocomplete>
                    </el-form-item>
                    <el-checkbox v-model="searchForm.checked"></el-checkbox>
                    <el-button type="primary" @click="search" icon="el-icon-search"></el-button>
                    <el-button @click="formClose('searchRef')" icon="el-icon-refresh-right"></el-button>
                </el-form>
            </el-row>
        </el-card>

        <el-card>
            <!-----------------------------------------------------------------  ------------------------------------------------ -->
            <el-row>
                <el-row type="flex" justify="space-around">
                    <el-col :span="20">
                        <div></div>
                    </el-col>
                    <el-col :span="2">
                        <el-button type="success" @click="showAddFormDialogVisible = true" icon="el-icon-plus"></el-button>
                    </el-col>
                </el-row>
                <el-table :data="stockList" border style="width: 100% " :row-class-name="tableRowClassName">
                    <el-table-column prop="Provider" label="" align="center"></el-table-column>
                    <el-table-column prop="Date" label="" align="center" :formatter="dataFormatter"> </el-table-column>
                    <el-table-column prop="Category" label="" align="center"> </el-table-column>
                    <el-table-column prop="Brand" label="" align="center"></el-table-column>
                    <el-table-column prop="Model" label="" align="center"></el-table-column>
                    <el-table-column prop="Price" label="()" align="center"></el-table-column>
                    <el-table-column prop="Quantity" label="()" align="center"> </el-table-column>
                    <el-table-column prop="Inventory" label="()" align="center"> </el-table-column>
                    <el-table-column prop="Remarks" label="" width="200px" align="center" class="remark">
                        <template slot-scope="scope">
                            <el-input v-model="scope.row.Remarks" class="in" autosize @change="addRemark(scope.row)" type="textarea"> </el-input>
                        </template>
                    </el-table-column>
                    <!--  -->
                    <el-table-column width="300px" scope label="" align="center">
                        <template slot-scope="scope">
                            <!--  -->
                            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
                                <el-button icon="el-icon-setting" type="primary" @click="showDditForm(scope.row)" circle></el-button>
                            </el-tooltip>
                            <!--  -->
                            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
                                <el-button
                                    icon="el-icon-s-promotion"
                                    type="success"
                                    @click="showOutStock(scope.row)"
                                    circle
                                    :disabled="scope.row.Inventory == 0 ? true : false"
                                ></el-button>
                            </el-tooltip>
                            <!--  -->
                            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
                                <el-popconfirm
                                    confirmButtonText=""
                                    cancelButtonText=""
                                    icon="el-icon-delete-solid"
                                    iconColor="red"
                                    title="?"
                                    @onConfirm="deleteStock(scope.row.Id)"
                                >
                                    <el-button slot="reference" type="danger" circle icon="el-icon-delete-solid"></el-button>
                                </el-popconfirm>
                            </el-tooltip>
                        </template>
                    </el-table-column>
                </el-table>
            </el-row>
        </el-card>

        <!---------------------------------------------------------------------------------------------------------------->
        <el-dialog title="" :visible.sync="showAddFormDialogVisible" width="30%" :rules="addFormRules" @close="formClose('addForm')">
            <el-form ref="addForm" :model="addForm" label-width="170px" :rules="addFormRules">
                <el-form-item label="" prop="Provider">
                    <el-input v-model="addForm.Provider"></el-input>
                </el-form-item>
                <el-form-item label="" prop="Date">
                    <el-date-picker
                        v-model="addForm.Date"
                        type="date"
                        placeholder=""
                        format="yyyy  MM  dd "
                        value-format="timestamp"
                        :picker-options="addForm.pickerOptions"
                    >
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="">
                    <el-cascader
                        v-model="addValue"
                        :options="options"
                        :props="{ value: 'Name', label: 'Name', children: 'Category', expandTrigger: 'hover' }"
                        @change="addChangeRef"
                        ref="cascader"
                    >
                    </el-cascader>
                </el-form-item>
                <el-form-item label="" prop="Model">
                    <el-input v-model="addForm.Model"></el-input>
                </el-form-item>
                <el-form-item label="()" prop="Price">
                    <el-input v-model.number="addForm.Price" oninput="value=value.replace(/[^\d{1,}\.\d{1,}|\d{1,}]/g,'')"></el-input>
                </el-form-item>
                <el-form-item label="()" prop="Quantity">
                    <el-input-number v-model="addForm.Quantity" label="" :min="0" :precision="0" controls-position="right"></el-input-number>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="showAddFormDialogVisible = false" icon="el-icon-close"></el-button>
                <el-button type="primary" @click="addStock" icon="el-icon-check"></el-button>
            </span>
        </el-dialog>

        <!---------------------------------------------------------------------------------------------------------------->
        <el-dialog title="" :visible.sync="showEditFormDialogVisible" width="30%" :rules="addFormRules" @close="formClose('editForm')">
            <el-form ref="editForm" :model="editForm" label-width="170px" :rules="addFormRules">
                <el-form-item label="" prop="Provider">
                    <el-input v-model="editForm.Provider"></el-input>
                </el-form-item>
                <el-form-item label="" prop="Date">
                    <el-date-picker
                        v-model="editForm.Date"
                        type="date"
                        placeholder=""
                        format="yyyy  MM  dd "
                        value-format="timestamp"
                        :picker-options="pickerOptions"
                    >
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="">
                    <el-cascader
                        v-model="editValue"
                        :options="options"
                        :props="{ value: 'Name', label: 'Name', children: 'Category', expandTrigger: 'hover' }"
                        @change="editChangeRef"
                        ref="editCascader"
                    >
                    </el-cascader>
                </el-form-item>
                <el-form-item label="" prop="Model">
                    <el-input v-model="editForm.Model"></el-input>
                </el-form-item>
                <el-form-item label="()" prop="Price">
                    <el-input v-model="editForm.Price" oninput="value=value.replace(/[^\d{1,}\.\d{1,}|\d{1,}]/g,'')" @change="changePrice"></el-input>
                </el-form-item>
                <el-form-item label="()" prop="Quantity">
                    <el-input-number v-model="editForm.Quantity" label="" :min="0" :precision="0" controls-position="right"></el-input-number>
                </el-form-item>
                <el-form-item label="()" prop="EditNum">
                    <el-input v-model="editForm.EditNum" label="" disabled></el-input>
                </el-form-item>
                <el-form-item label="" prop="Remarks">
                    <el-input v-model="editForm.Remarks" label="" type="textarea" autosize></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="showEditFormDialogVisible = false" icon="el-icon-close"></el-button>
                <el-button type="primary" @click="editFormHandler" icon="el-icon-check"></el-button>
            </span>
        </el-dialog>

        <!---------------------------------------------------------------------------------------------------------------->
        <el-dialog title="" :visible.sync="outStockFormDialogVisible" width="30%" @close="formClose('outStockForm')">
            <el-form ref="outStockForm" :model="outStockForm" label-width="170px" :rules="addFormRules">
                <el-form-item label="">
                    <el-input v-model="outStockForm.Provider" disabled></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="outStockValue" disabled></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="outStockForm.Model" disabled></el-input>
                </el-form-item>
                <el-form-item label="()">
                    <el-input v-model="outStockForm.Inventory" disabled></el-input>
                </el-form-item>
                <el-form-item label="()">
                    <el-input v-model="outStockForm.Price" disabled></el-input>
                </el-form-item>

                <el-form-item label="" prop="Shipper">
                    <el-input v-model="outStockForm.Shipper" label=""></el-input>
                </el-form-item>

                <el-form-item label="" prop="OutDate">
                    <el-date-picker
                        v-model="outStockForm.OutDate"
                        type="date"
                        placeholder=""
                        format="yyyy  MM  dd "
                        value-format="timestamp"
                        :picker-options="pickerOptions"
                    >
                    </el-date-picker>
                </el-form-item>

                <el-form-item label="()" prop="Quantity">
                    <el-input-number
                        v-model="outStockForm.Quantity"
                        label=""
                        :min="0"
                        :precision="0"
                        controls-position="right"
                    ></el-input-number>
                </el-form-item>

                <el-form-item label="()" prop="Sell">
                    <el-input v-model="outStockForm.Sell" label="" oninput="value=value.replace(/[^\d{1,}\.\d{1,}|\d{1,}]/g,'')"> </el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="outStockFormDialogVisible = false" icon="el-icon-close"></el-button>
                <el-button type="primary" @click="outStock" icon="el-icon-check"></el-button>
            </span>
        </el-dialog>
    </section>
</template>
<script>
import datas from './datas.js'
import methods from './methdos.js'
import util from '../../common/js/util.js'

export default {
    data() {
        return datas.init()
    },
    created() {
        this.getList()
    },
    methods: methods
}
</script>

<style scoped>
.el-table {
    margin-top: 15px;
}

.el-table .el-table-column {
    padding: 0px;
}

.el-col {
    margin: 0 20px;
}
.el-button {
    margin: 0 6px;
}

.coll {
    position: relative;
}

.el-table /deep/ .over {
    background-color: #ffd700;
}

.el-table /deep/ .cell {
    white-space: pre-wrap;
}

.el-card /deep/ .el-card__body {
    padding: 5px;
    padding-top: 10px;
    margin-top: 10px;
}

.in /deep/ .el-textarea__inner {
    border: none;
    resize: none;
    padding: 0;
    font-size: 14px;
}

.el-form /deep/ .el-input__inner {
    width: 240px;
}
.el-form /deep/ .el-input-number {
    width: 240px;
}

/* .addStock /deep/ .el-input-number .el-input--suffix {
    width: 240px;
} */
</style>



